.stars {
  display: flex;

  &__star {
    height: 16px;
    width: 16px;
    background-image: url(/images/star.svg);
    background-position: center;
    background-repeat: no-repeat;
    margin-right: 4px;
  }

  &--1 > :first-child,
  &--2 > :nth-child(-n+2),
  &--3 > :nth-child(-n+3),
  &--4 > :nth-child(-n+4),
  &--5 > :nth-child(-n+5) {
    background-image: url(/images/star-active.svg);
  }
}
